import React from 'react'
import { MdShoppingCart } from "react-icons/md";
import { useSelector, useDispatch } from 'react-redux'
import { addFood } from '../../redux/actions/basketActions';
import './basket.css'

const Basket = () => {
    const [open, setOpen] = React.useState(true)
    const basket = useSelector((state) => state.basket.basket)
    const dispatch = useDispatch()
    console.log(basket)

    dispatch(addFood({ id: 22, count: 1 }))

    return (
        <ul className='site-header-cart' onPointerEnter={() => setOpen(!open)} onPointerLeave={() => setOpen(!open)}>
            <li className='cart-item'>
                <a href="#"></a>
                <MdShoppingCart />
                <span className='count'>o</span>
            </li>
            <li className={open ? 'cart-item-dropdown hidden' : 'cart-item-dropdown'}>
                <div className='cart_dropdown'>
                    <p className='cart_dropdown__item'>Ваша корзина пуста</p>
                </div>
            </li>
        </ul>
    )
}

export default Basket
